import { io } from 'socket.io-client';
// Instance socket pour le chat
let chatSocket = null;
// Vérifie la connexion utilisateur via Socket.IO
export async function isUserLoggedIn() {
    return new Promise((resolve) => {
        console.log('🔍 Vérification auth via socket...');
        // Si pas de socket, créer une connexion
        if (!chatSocket) {
            const ip = window.location.hostname;
            const protocol = window.location.protocol === 'https:' ? 'https' : 'http';
            const socketUrl = `${protocol}://${ip}:8080/chat`;
            console.log('🚀 Création nouvelle socket:', socketUrl);
            chatSocket = io(socketUrl, {
                withCredentials: true,
                autoConnect: true,
                timeout: 10000, // 10 secondes de timeout
                transports: ['websocket', 'polling'] // Fallback sur polling si websocket échoue
            });
            // Écouter les événements de connexion
            chatSocket.on('connect', () => {
                console.log('✅ Socket connectée:', chatSocket?.id);
            });
            chatSocket.on('connect_error', (error) => {
                console.log('❌ Erreur connexion socket:', error);
                resolve(false);
            });
            chatSocket.on('disconnect', (reason) => {
                console.log('🔌 Socket déconnectée:', reason);
            });
        }
        // Attendre que la socket soit connectée avant de vérifier l'auth
        const checkAuth = () => {
            if (chatSocket?.connected) {
                console.log('📡 Envoi check-auth...');
                chatSocket.emit('check-auth', (response) => {
                    console.log('📨 Réponse auth socket:', response);
                    resolve(response?.authenticated === true);
                });
            }
            else {
                console.log('⏳ Socket pas encore connectée, retry dans 100ms...');
                setTimeout(checkAuth, 100);
            }
        };
        // Commencer la vérification
        setTimeout(checkAuth, 100); // Petit délai pour laisser la socket se connecter
    });
}
// Initialise le chat si l'utilisateur est connecté
export async function initChatIfLoggedIn() {
    console.log('🎯 initChatIfLoggedIn() appelée');
    if (await isUserLoggedIn()) {
        console.log('👤 Utilisateur connecté - Affichage du chat');
        await displayChatButton();
    }
    else {
        console.log('❌ Utilisateur non connecté - Masquage du chat');
        hideChatButton();
    }
}
// Afficher le bouton de chat après connexion
export async function displayChatButton() {
    if (document.getElementById('chat-fab')) {
        console.log('Bouton chat déjà présent');
        return;
    }
    console.log('✅ Création du bouton chat après connexion');
    const { ChatFloatingButton } = await import('../views/chat.views');
    document.body.insertAdjacentHTML('beforeend', ChatFloatingButton());
    const { chatManager } = await import('../chat/chat');
}
// Masquer le bouton de chat après déconnexion
export function hideChatButton() {
    const chatButton = document.getElementById('chat-fab');
    const chatPanel = document.getElementById('chat-panel');
    if (chatButton) {
        chatButton.remove();
        console.log('❌ Bouton chat supprimé après déconnexion');
    }
    if (chatPanel) {
        chatPanel.remove();
    }
    // Nettoyer la socket lors de la déconnexion
    if (chatSocket) {
        chatSocket.disconnect();
        chatSocket = null;
        console.log('🔌 Socket chat fermée');
    }
}
